body {
  margin: 0;
  padding: 0;
  font-family: 'Satoshi-Bold', sans-serif;
  background-color: #fcfcfc;
}

.container-nav {
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
  }

.navbar {
  height: 60px;
  background-color: white;
  position: relative;
  display: flex;
  
}

.logo {
    margin-right:500px; /* pushes the logo to the left */
  }

.menu-icon {
  display: none;
  
}

.nav-elements {
    margin-left: auto; /* pushes the menu items to the right */
  }

.nav-elements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.nav-elements ul li:not(:last-child) {
  margin-right: 60px;
}

.nav-elements ul a {
  font-size: 16px;
  font-weight: 400;
  color: #2f234f;
  text-decoration: none;
  
  
}
.om-mig  {
    white-space: nowrap;
  }

.nav-elements ul a.active {
  color: #574c4c;
  font-weight: 500;
  position: relative;
}

.nav-elements ul a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #574c4c;
}

@media (max-width: 768px) {
  .nav-elements ul li:not(:last-child) {
    margin-right: 30px;
  }

  .menu-icon {
    display: none;
    cursor: pointer;
    width: 30px;
    height: 30px;
  }
  .menu-icon svg {
    width: 24px;
    height: 24px;
  }

}

@media (max-width: 600px) {
  .menu-icon {
    display: block;
    cursor: pointer;
    width: 30px;
    height: 30px;
  }

  .logo {
    margin-right:260px; /* pushes the logo to the left */
  }

  .menu-icon svg {
    width: 24px;
    height: 24px;
  }

  .nav-elements {
    position: absolute;
    right: 0;
    top: 60px;
    background-color: white;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
  }

  .nav-elements.active {
    width: 270px;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }

  .nav-elements ul li {
    margin-right: unset;
    margin-top: 22px;
  }
}